                  h5 1. Ubicar el molde base
                  p Colocar el molde base de la prenda superior femenina sobre una superficie plana. Asegurarse de tener visibles las lneas del centro delantero y centro espalda, as como el hombro y el escote original.
              .row.justify-content-center
