                  p Colocar sobre una superficie plana el molde base de prenda superior femenina, compuesto por el delantero (a la izquierda) y la espalda (a la derecha). Identificar claramente las lneas gua: centro delantero, centro espalda, hombros y escotes originales. Estas referencias permiten orientar correctamente el molde, incluso con apoyo tctil o gua verbal.
              .row.justify-content-center
                .col-md-11
                  h5 2. Trazar el escote en V en el delantero
                  p.mb-1 En el centro delantero (lnea vertical del borde derecho del molde delantero):
                  p.mb-1 Medir hacia abajo entre 7 y 10 centmetros, segn la profundidad deseada del escote.
