      h4.mb-0 Escote barco o bandeja

    p.mb-4 Se extiende en lnea horizontal desde un hombro al otro, cubriendo parte de estos. Ideal para equilibrar proporciones en cuerpos con cadera ms amplia.

    .row.justify-content-center.mb-5
