                  img.w-55.mb-4(src="@/assets/curso/temas/t2/ico.svg")
                  h5 1. Preparacin del molde
                  p La persona trabaja sobre el molde base del cuerpo femenino, con las piezas delantera (izquierda) y posterior (derecha) extendidas sobre una superficie plana.
              .row.justify-content-center
                .col-md-11
                  h5 2. Escote en el delantero
                  p En la pieza delantera, se marca un punto en la lnea del hombro, aproximadamente a 3 o 4 cm desde el cuello hacia el extremo del hombro. Luego, se traza una curva suave desde ese punto hacia el centro delantero, ubicndola cerca del inicio del busto. Esta curva debe ser ancha, horizontal y poco profunda, manteniendo un trazo limpio y armnico.
              .row.justify-content-center
                .col-md-11
                  h5 3. Escote en la espalda
                  p En la espalda, se repite un trazado similar. Se marca desde un punto cercano al hombro hacia el centro de espalda, formando una curva poco pronunciada pero alineada con el estilo horizontal del escote bandeja.
              .row.justify-content-center
                .col-md-11
                  h5 4. Revisin y simetra
                  p La persona verifica que ambas curvas delantera y trasera estn a la misma altura sobre la lnea de hombros, y que conecten suavemente con el contorno de la prenda.
              .row.justify-content-center
                .col-md-11
                  h5 5. Corte del nuevo escote
                  p Una vez definidas las lneas, se recortan sobre el trazo nuevo, eliminando el escote original del molde base.
              .row.justify-content-center
                .col-md-11
                  h5 6. Agregado de margen de costura (opcional)
                  p.mb-1 Si se va a confeccionar, se recomienda aadir entre 0.7 y 1 cm de margen de costura en todo el contorno del escote.
                  p Este tipo de escote es comn en blusas formales, vestidos de fiesta y prendas que buscan resaltar los hombros. Ofrece un diseo elegante y equilibrado, ideal para estilos romnticos o sofisticados.
