                  h5 1. Preparacin del molde
                  p La persona ubica sobre una superficie plana el molde base del cuerpo femenino, que incluye la pieza delantera (a la izquierda) y la posterior (a la derecha).
              .row.justify-content-center
                .col-md-11
                  h5 2. Definicin del nuevo escote
                  p Desde el punto ms profundo del escote delantero, ubicado en el centro del molde, se traza una curva ascendente hacia el extremo superior de la sisa del delantero. Esta lnea continua de forma fluida y simtrica hacia la sisa del molde posterior, formando una curva continua que rodea el cuello.
              .row.justify-content-center
                .col-md-11
                  h5 3. Revisin del trazo
                  p La persona verifica que la lnea forme una curva armoniosa desde el centro delantero hasta los hombros, sin quiebres ni cambios bruscos de direccin. La forma debe sugerir un corte que deja el cuello libre y cubre el busto con una curva elegante.
              .row.justify-content-center
                .col-md-11
                  h5 4. Corte del escote
                  p Una vez trazada la curva, se recorta siguiendo la nueva lnea del escote, eliminando el escote original del molde base.
              .row.justify-content-center
                .col-md-11
                  h5 5. Margen de costura (si aplica)
                  p.mb-1 Para confeccin, se recomienda agregar un margen de costura de entre 0.7 y 1 cm a lo largo del nuevo escote.
                  p Este escote es tpico en blusas, tops y vestidos de verano o fiesta, y se ajusta normalmente con tiras o cuello alto en la parte posterior, resaltando hombros y espalda.
