        p Los cuellos suelen ser un elemento esencial al momento de construir un patrn que permita el desarrollo de modelos con variaciones basadas en este tipo de plano, a continuacin, se presentan los ms implementados en prendas de moda casual. 

    .subt2.mb-4.py-2.px-4(data-aos="fade")
      h4.mb-0 Cuello cascada

    .row.justify-content-center.mb-5
      .col-lg-5.col-md-7.mb-4.mb-lg-0(data-aos="fade-right")
        .titulo-sexto.color-acento-contenido.mb-3
          h5 Figura 14. 
          span Cuello cascada
        figure.mb-2
          img(src="@/assets/curso/temas/t2/fig14.svg" alt="Presenta cuatro pasos para desarrollar un escote con volante circular. En el primero, se trazan lneas desde el escote en un molde base. En el segundo, se recortan y separan las secciones para aadir amplitud. En el tercero, las partes abiertas se distribuyen en forma circular. En el cuarto, se obtiene una sola pieza con forma espiral que conforma el volante.")
        figcaption Fuente: SENA, (2021)
      .col-lg-7(data-aos="fade-left")
        LineaTiempoD.color-primario
          .row(numero="1" titulo="Paso 1: preparacin y trazado base") 
            .col-12
              p.mb-1 En la primera figura (arriba izquierda), se parte del molde base del delantero.
              p.mb-1 Se ampla el escote bajando entre 7 y 12 cm desde la base del cuello.
              p.mb-1 Se marca un punto en la sisa, aproximadamente 3 cm debajo del punto original del hombro.
              p.mb-1 Desde ese punto y desde el nuevo escote, se trazan lneas diagonales hacia un punto de giro ubicado cerca del busto (altura de pinza).
              p.mb-0 Se marca una lnea desde el hombro hacia el centro del busto como gua de apertura.
          .row(numero="2" titulo="Paso 2: corte y apertura") 
            .col-12
              p.mb-1 En la segunda figura (arriba derecha), se recortan las lneas desde el hombro hacia el punto de giro.
              p.mb-1 Se abren en forma de abanico, separando cada segmento entre 2 y 4 cm, segn el volumen deseado.
              p.mb-0 Este paso crea el volumen necesario para la cada del tejido.
          .row(numero="3" titulo="Paso 3: desarrollo del cuello") 
            .col-12
              p.mb-1 En la tercera figura (abajo izquierda), se presenta cmo las piezas abiertas generan una nueva forma.
              p.mb-1 Al redibujar el contorno superior, se forma la curva del escote en cascada.
              p.mb-0 La lnea del hombro puede mantenerse o eliminarse si se desea un diseo sin costura en el hombro.
          .row(numero="4" titulo="Paso 4: pieza final del cuello cascada") 
            .col-12
              p.mb-1 En la figura cuatro (abajo derecha), se representa el molde final del cuello cascada, con forma de espiral o media luna.
              p.mb-1 Este molde se coloca al sesgo (diagonal del tejido) para lograr una cada natural.
              p.mb-0 La curva ms cerrada corresponde al escote, mientras que la parte ms amplia es la que genera el vuelo.
          .row(numero="5" titulo="Consideraciones tcnicas") 
            .col-12
              p.mb-1 Este tipo de cuello no requiere vistas, ya que se forma por el mismo tejido.
              p.mb-1 Se recomienda usar telas livianas y con buena cada como seda, viscosa o punto fino.
              p.mb-0 El molde se corta en una sola pieza y se dobla sobre el escote, generando una cada fluida sin costuras visibles.


