      h4.mb-0 Cuello ner
    
    .row.justify-content-center.mb-5
      .col-lg-6.mb-4.mb-lg-0(data-aos="fade-right")
        .tarjeta.bg-5.p-4.h-100
          .mb-4
            .titulo-sexto.color-acento-contenido.mb-3
              h5 Figura 15. 
              span Cuello ner bsico 
            figure.mb-2
              img(src="@/assets/curso/temas/t2/fig15.png" alt="Representa el diseo de un cuello tipo mao. A la izquierda, se indica su ubicacin alrededor del cuello de una figura humana estilizada. A la derecha, se presenta el desarrollo plano del cuello: una pieza alargada con ligeras curvas en sus extremos y medidas especficas en centmetros. El borde identificado como CF corresponde al centro frente y CA al centro espalda.")
            figcaption Fuente: SENA, (2021)
          p.mb-0 El extremo izquierdo est marcado como CA (centro de espalda).
          p El extremo derecho como CF (centro delantero).
          ul.lista-ul--color.color-primario.fa-lista
            li
              i.fas.fa-check-circle
              span.mb-0 Se indica una elevacin de 0.5 cm en el centro delantero (CF) y una bajada de 0.3 cm en el centro de espalda (CA), lo que da forma al contorno superior del cuello para ajustarse cmodamente al cuello humano.
            li
              i.fas.fa-check-circle
              span.mb-0 Tambin se agrega una bajada de 0.3 cm en la parte inferior del CF para que el cuello cierre mejor al frente.
            li
              i.fas.fa-check-circle
              span.mb-0 El alto total del cuello es de 4 cm.
      .col-lg-6.mb-4.mb-lg-0(data-aos="fade-left")
        .tarjeta.bg-5.p-4.h-100
          .mb-4
            .titulo-sexto.color-acento-contenido.mb-3
              h5 Figura 16. 
              span Cuello ner variacin 
            figure.mb-2
              img(src="@/assets/curso/temas/t2/fig16.png" alt="El diseo de un cuello camisero con forma estilizada. A la izquierda, se visualiza su colocacin alrededor del cuello en una figura humana neutra. A la derecha, se presenta el desarrollo plano del cuello: una pieza curva con medidas detalladas en centmetros. El extremo derecho, marcado como CF, indica el centro frente y el izquierdo como CA, centro espalda. Lneas punteadas en la base sealan la forma interna del cuello doblado.")
            figcaption Fuente: SENA, (2021)
          ul.lista-ul--color.color-primario.fa-lista
            li
              i.fas.fa-check-circle
              span.mb-0 La base del cuello tiene 4 cm de alto, medido desde el centro de espalda (CA) hasta el centro delantero (CF). #[br]
                | En el centro delantero, la curva del contorno superior se eleva 0.5 cm y forma una cada de 3.5 cm desde esa altura hasta el punto medio del delantero.
            li
              i.fas.fa-check-circle
              span.mb-0 La curva inferior tambin desciende ligeramente hasta 2 cm en el CF para reforzar el efecto visual de cada.
            li
              i.fas.fa-check-circle
              span.mb-0 La lnea superior y la inferior estn curvadas, lo que da al cuello un acabado ms fluido que en el diseo tradicional.


