      h4.mb-0 Cuello beb

    .row.justify-content-center.mb-5
      .col-lg-5.col-md-6.mb-4.mb-lg-0(data-aos="fade-right")
        .titulo-sexto.color-acento-contenido.mb-3
          h5 Figura 17. 
          span Cuello beb
        figure.mb-2
          img(src="@/assets/curso/temas/t2/fig17.png" alt="Dibujo tcnico del patrn para un cuello beb, que muestra las piezas del delantero y posterior con medidas especficas para trazar elcontorno del cuello.")
        figcaption Fuente: SENA, (2021)
      .col-lg-7(data-aos="fade-left") 
        .tarjeta-numerada.color-primario.bg-2.sin-borde.p-5.py-4.mb-3
          .tarjeta-numerada__numero
            .h2 1
          h5 Preparacin del molde base
          p.mb-0 La persona trabaja sobre el molde base delantera y posterior, que incluye el escote y el hombro.
        .tarjeta-numerada.color-primario.bg-2.sin-borde.p-5.py-4.mb-3
          .tarjeta-numerada__numero
            .h2 2
          h5 Delimitacin del cuello posterior
          p.mb-0 Se mide 5.5 cm desde la base del cuello hacia afuera, siguiendo la lnea del hombro posterior.
          p.mb-0 Luego se marca una bajada de 0.5 cm desde el escote posterior.
          p.mb-0 Estas dos medidas se unen con una lnea curva suave, formando la parte trasera del cuello.
        .tarjeta-numerada.color-primario.bg-2.sin-borde.p-5.py-4.mb-3
          .tarjeta-numerada__numero
            .h2 3
          h5 Trazado del cuello delantero
          p.mb-0 En el molde delantero, se mide 6.5 cm sobre la lnea del hombro y 4 cm hacia abajo desde el escote. Tambin se marca una separacin de 0.5 cm en el punto inicial del escote delantero para suavizar la entrada del cuello.
          p.mb-0 Uniendo esos puntos, se forma la parte delantera del cuello con una curva amplia y redondeada.
        .tarjeta-numerada.color-primario.bg-2.sin-borde.p-5.py-4.mb-3
          .tarjeta-numerada__numero
            .h2 4
          h5 Punto de giro en el hombro
          p.mb-0 En el encuentro entre delantero y posterior, sobre la lnea del hombro, se genera una articulacin que debe coincidir con la costura del hombro real.
          p.mb-0 Esto asegura que el cuello beb se acomode de forma plana sobre la prenda.
        .tarjeta-numerada.color-primario.bg-2.sin-borde.p-5.py-4
          .tarjeta-numerada__numero
            .h2 5
          h5 Ancho del cuello (opcional)
          p.mb-1 En algunos casos, se puede definir un ancho constante de 3 cm desde el borde del escote, como se ve en una de las lneas gua en la figura.
          p Caractersticas y recomendaciones:
          ul.lista-ul--color.color-primario.fa-lista.mb-0
            li.mb-1
              i.fas.fa-check-circle
              span.mb-0 El cuello beb se corta por separado y se cose con vista o forro.
            li.mb-1
              i.fas.fa-check-circle
              span.mb-0 Es ideal para telas livianas o de peso medio, que permitan que el cuello conserve su forma.
            li.mb-0
              i.fas.fa-check-circle
              span.mb-0 Puede ser simtrico o asimtrico, y terminar con punta redonda, cuadrada o en pico, segn el diseo.

