    .row.justify-content-center.mb-5
      .col-lg-5.col-md-7.mb-4.mb-lg-0(data-aos="fade-right")
        .titulo-sexto.color-acento-contenido.mb-3
          h5 Figura 18. 
          span Capota 
        figure.mb-2
          img(src="@/assets/curso/temas/t2/fig18.png" alt="El desarrollo tcnico de una capucha con su respectiva adaptacin al patrn superior. En la parte superior se encuentra el trazo de la capucha, con medidas detalladas en centmetros y flechas que indican direccin del hilo. En la parte inferior, aparecen los moldes del cuerpo etiquetados como Espalda y Frente, con ajustes en el escote para integrar la capucha. Se seala tambin la ubicacin del cierre con la palabra Zipper y valores milimtricos para precisin en el ensamblaje.")
        figcaption Fuente: SENA, (2021)
