        AcordionA.style2(tipo="a" clase-tarjeta="tarjeta color-primario")
          .row.justify-content-center(titulo="1. Trazado de la capota (pieza superior)")
            .col-11
              p.mb-0 En la parte superior de la figura se encuentra el molde principal de la capota, representado como una sola pieza vista de perfil.
              p.mb-0 Ancho total: 25 cm (medido desde la parte posterior hasta el centro frontal).
              p Altura total: 35 cm (desde la base del cuello hasta la coronilla).
              ul.lista-ul--color.color-primario.fa-lista.mb-0
                li
                  i.fas.fa-check-circle
                  span.mb-0 #[strong Curva superior:] inicia a 7 cm desde el borde izquierdo y se redondea hasta encontrarse con la lnea vertical derecha.
                li.mb-0
                  i.fas.fa-check-circle
                  span.mb-0 #[strong Parte frontal:] se marca una cada de 11 cm hacia abajo y se curva suavemente, representando la apertura del rostro.
                    p.mb-0 Se indican medidas de 0.5 cm hacia adentro en la parte frontal y posterior para costura o forma.
                    p.mb-0 El centro del molde tiene un doblez (marcado con flecha), indicando que esta pieza se corta doble o en tela doblada.
                    p.mb-0 Una lnea curva gua el contorno superior del crneo y la cada de la capucha.
          .row.justify-content-center(titulo="2. Piezas base de unin: espalda y frente")
            .col-11
              p En la parte inferior de la figura se presentan las piezas del cuerpo donde se unir la capota.
              ul.lista-ul--color.color-primario.fa-lista.mb-0
                li
