                    p.mb-0 Esta modificacin permite el encaje suave de la parte posterior de la capota.
                li
                  i.fas.fa-check-circle
                  span.mb-0 #[strong Frente:] se baja tambin 0.7 cm y se ampla 3 cm hacia el hombro.
