    .subt2.mb-4.py-2.px-4(data-aos="fade")
      h4.mb-0 Cuello y solapa
    
    .row.justify-content-center.mb-5
      .col-lg-6.mb-4.mb-lg-0(data-aos="fade-right")
        p Este proceso corresponde al desarrollo de un cuello sastre con solapa, tpico en #[em blazers], chaquetas o abrigos. El diseo permite que el cuello se doble hacia afuera, formando una solapa visible en el delantero. La figura presenta una secuencia de pasos que permiten construir correctamente esta estructura.
        LineaTiempoD.color-primario
          .row(numero="1" titulo="Paso 1: identificacin de piezas base") 
            .col-12
              p.mb-0 A la izquierda, se indican las partes que componen el cuello y la solapa:
              p.mb-0 Pieza superior del cuello (nmero 4).
              p.mb-0 Pieza inferior de soporte (nmero 5).
              p.mb-0 Solapa delantera dividida en varias secciones que se ensamblan sobre la lnea del escote.
          .row(numero="2" titulo="Paso 2: transformacin del escote y montaje de solapa") 
            .col-12
              p.mb-0 En el centro de la figura:
              p.mb-0 Se parte del delantero con pinza busto.
              p.mb-0 Sobre el hombro se traza un desplazamiento de 5.5 cm hacia afuera para ubicar el punto de quiebre del cuello.
              p.mb-0 Se gira la pieza superior (pieza 1) en distintos ngulos, numerados del 1 al 5, para visualizar cmo se comporta el quiebre de la solapa y encontrar el punto correcto de ajuste.
              p.mb-0 El cuello se curva hacia el centro delantero (zona marcada con el nmero 5 en la base).
          .row(numero="3" titulo="Paso 3: integracin de cuello y solapa") 
            .col-12
              p.mb-0 En la tercera figura (derecha):
              p.mb-0 Se confirma la forma definitiva del cuello y la solapa integradas al delantero.
              p.mb-0 Se destacan dos lneas: la que forma el cuello exterior (nmero 2) y la que da forma a la solapa frontal (nmero 3).
              p.mb-0 Ambas lneas deben armonizar con el escote original y respetar el punto de quiebre.
          .row(numero="4" titulo="Paso 4: separacin de piezas para confeccin") 
            .col-12
              p.mb-0 Las piezas deben separarse para su corte, considerando mrgenes de costura. Se cortan por separado:
              p.mb-0 El cuello (parte superior y base).
              p.mb-0 La solapa.
              p.mb-0 El delantero con su transformacin final.
          .row(numero="5" titulo="Recomendaciones tcnicas") 
            .col-12
              ul.lista-ul--color.color-primario.fa-lista
                li
                  i.fas.fa-check-circle
                  span.mb-0 Marcar claramente la lnea de quiebre (donde se dobla la solapa).
                li
                  i.fas.fa-check-circle
                  span.mb-0 El cuello suele cortarse doble y con entretela para mayor estructura.
                li.mb-0
                  i.fas.fa-check-circle
                  span.mb-0 Este tipo de diseo es ideal para prendas formales o estructuradas.
      .col-lg-6.col-md-9(data-aos="fade-left")
        .titulo-sexto.color-acento-contenido
          h5 Figura 19. 
          span Cuello y solapa
        figure.mb-2
          img(src='@/assets/curso/temas/t2/fig19.svg', alt='Representa el proceso para transformar un cuello plano en un diseo con volumen. Se muestran piezas numeradas que se abren y redirigen para generar una forma curva. Luego, se integran al molde superior mediante lneas y medidas que orientan su ubicacin con precisin.')  
        figcaption Fuente: SENA, (2021)


    .subt2.mb-4.py-2.px-4(data-aos="fade")
      h4.mb-0 Cuello alto con manga ragln

    p.mb-4 Este diseo combina un cuello alto estructurado con una manga ragln, caracterizada por su lnea continua desde el cuello hasta la sisa. Es un recurso habitual en prendas deportivas, abrigos, buzos o chaquetas, ya que permite libertad de movimiento y una apariencia limpia sin costura de hombro tradicional.

    .row.bgc-3.align-items-center.p-2.p-md-3.mb-4(data-aos="fade-down")
      .col-lg-12.col-12.px-lg-4
        .row.justify-content-center.align-items-center

          .row.justify-content-center.mb-4
            .col-lg-6.col-md-9.mb-4
              .titulo-sexto.color-acento-contenido(data-aos='fade-right')
                h5 Figura 20. 
                span  Cuello alto con manga ragln 
              figure.mb-2
                img(src='@/assets/curso/temas/t2/fig20.png', alt='Presenta el ajuste de hombros para una prenda superior con manga rangln. A la izquierda se encuentra la vista posterior y a la derecha la vista delantera. Ambas incluyen lneas curvas y puntos numerados que marcan desplazamientos en centmetros para dar forma a la unin entre hombro, escote y sisa. Las lneas punteadas indican el contorno original antes de la modificacin.')  
              figcaption Fuente: SENA, (2021)
            .col-lg-6.col-md-9.mb-4
              .titulo-sexto.color-acento-contenido(data-aos='fade-right')
                h5 Figura 21. 
                span Cuello manga rangln 
              figure.mb-2
                img(src='@/assets/curso/temas/t2/fig21.png', alt='Presenta el trazo de una manga rangln. A la izquierda se encuentra la seccin posterior y a la derecha la seccin delantera. Ambas partes contienen lneas rectas y curvas acompaadas por valores numricos en centmetros. Estas lneas indican cmo se construye la unin entre el borde superior del cuerpo, la abertura para el brazo y el contorno del cuello. Lneas segmentadas marcan la forma original antes de aplicar los ajustes.')  
              figcaption Fuente: SENA, (2021)

          .row(data-aos="fade-down")
            .col-md-6.col-lg.mb-3.mb-lg-0
              .tarjeta-avatar.numero
                span.num 1
                .tarjeta.bg-1
                  .p-4
                    h5 Trazado de la pieza posterior
                    p.mb-0 La figura izquierda presenta el molde posterior.
                    p.mb-0 Desde el punto base del cuello hacia la lnea del hombro, se marca una elevacin de 2.5 cm.
                    p.mb-0 Luego, hacia el costado, se mide 0.5 cm y se une con una lnea curva hasta el escote posterior.
                    p.mb-0 Este nuevo contorno define la base del cuello alto posterior.
                    p.mb-0 A partir del mismo punto, se traza una lnea curva hacia la sisa, que ser la lnea de manga ragln.
                    p.mb-0 Esta lnea reemplaza al hombro tradicional y conecta suavemente con la curva del cuerpo.
            .col-md-6.col-lg.mb-3.mb-lg-0
              .tarjeta-avatar.numero
                span.num 2
                .tarjeta.bg-1
                  .p-4
                    h5 Trazado de la pieza delantera
                    p.mb-0 La figura derecha representa el delantero.
                    p.mb-0 Desde el cuello hacia el hombro se suben 1.5 cm, y hacia el costado se marcan 2 cm.
                    p.mb-0 Estos puntos se conectan con una lnea curva que redefine el escote delantero.
                    p.mb-0 La lnea de la manga ragln se dibuja desde este nuevo punto elevado hasta la sisa, generando una curva que se alinea con la pieza trasera.
                    p.mb-0 Se presenta una ligera inclinacin hacia el centro delantero, formando parte del ajuste del cuello alto.
            .col-md-6.col-lg.mb-3.mb-lg-0
              .tarjeta-avatar.numero
                span.num 3
                .tarjeta.bg-1
                  .p-4
                    h5 Cuello alto
                    p.mb-0 El cuello alto se desarrolla a partir de las nuevas lneas del escote.
                    p.mb-0 Puede tener una altura variable (generalmente entre 5 y 10 cm).
                    p.mb-0 Se corta doble o con vista, y puede llevar entretela segn el tipo de tejido.

    .row.justify-content-center.mb-4(data-aos='fade')
      .col-lg-10
        .bg-2.p-4
          .row.justify-content-center.align-items-center
            .col-lg-2.d-none.d-lg-block
              img(src="@/assets/curso/temas/t2/8.svg")
            .col-lg-9
              h5 Caractersticas clave del diseo
              p.mb-0 El cuello alto proporciona abrigo y estructura en la parte superior de la prenda.
              p.mb-0 La manga ragln elimina la costura tradicional del hombro, dando comodidad y continuidad en el diseo.
              p.mb-0 Ideal para telas de punto, pao, algodn perchado o tejidos con cuerpo.

    Separador

    #t_2_1.titulo-segundo.color-acento-contenido(data-aos='fade-right')
      h2 2.1 Variaciones de mangas 

    .row.align-items-center.mb-5
      .col-lg-1.col-md-2.col-3(data-aos="fade-right")
        img(src="@/assets/curso/temas/t2/9.svg")
      .col-lg-11.col-md-10.col-9(data-aos="fade-left")
        p.mb-0 Dentro del diseo de moda femenina, las mangas representan un componente esencial tanto a nivel funcional como esttico. Las modificaciones en su patronaje permiten crear siluetas diversas, adaptadas a diferentes estilos, estaciones del ao y necesidades de comodidad. Al variar la forma, el volumen o la longitud de las mangas, se logra transformar por completo el carcter de una prenda sin alterar su estructura base.

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-5.col-md-8.mb-3.mb-lg-0(data-aos="fade-right")
        img(src="@/assets/curso/temas/t2/10.png")
      .col-lg-7(data-aos="fade-left")
        p Entre las variaciones ms utilizadas se encuentran la manga japonesa, que extiende el hombro de forma continua sin costura en la sisa, generando una cada natural del tejido. Tambin destaca la manga abullonada, que incorpora frunces en uno o ambos extremos, aportando dramatismo y volumen. Por otro lado, la manga murcilago se distingue por su gran amplitud desde el hombro hasta la cintura, ofreciendo libertad de movimiento y un diseo ms relajado.
        .bg-3.p-4
          .row.justify-content-center.align-items-center
            .col-lg-10
              p.mb-0 Estas transformaciones no solo enriquecen visualmente las prendas, sino que tambin mejoran su ajuste al cuerpo, incrementan la funcionalidad del diseo y responden a las preferencias individuales de quienes las visten. Asimismo, permiten a las personas que disean ropa explorar nuevas formas y reinterpretar estilos clsicos con creatividad e innovacin.
            .col-lg-2.d-none.d-lg-block
              img(src="@/assets/curso/temas/t2/11.svg")


