      h2 2.2. Transformaciones de sisas y mangas

    p.mb-4 A continuacin, se presentan diferentes variaciones de manga a partir de la modificacin de sisa en el patrn, se debe tener en cuenta que la copa de la manga se afecta a medida.

    .row.justify-content-center.mb-5
      .col-lg-8.mb-4.mb-lg-0(data-aos="fade-right")
        .titulo-sexto.color-acento-contenido.mb-3
          h5 Figura 22. 
