        figure.mb-2
          img(src="@/assets/curso/temas/t2/fig22.svg" alt="Representa el escalado de una prenda superior con manga. A la izquierda se encuentran los trazos del cuerpo posterior y delantero; a la derecha, el trazo de la manga. Cada seccin muestra varias tallas superpuestas mediante lneas curvas y rectas que parten desde un punto comn, identificadas con letras y nmeros.")
        figcaption Fuente: SENA, (2021)
      .col-lg-4.mt-lg-5(data-aos="fade-left")
        h4 Patrones de la figura 22
        p A la izquierda se presentan los moldes del cuerpo posterior y delantero, con lneas de sisa que se curvan cada vez ms hacia abajo y hacia el costado. Estas lneas estn marcadas desde la lnea A (ms alta y ajustada) hasta la lnea E (ms baja y amplia).
        p A la derecha, se ilustra el desarrollo de la manga ragln, donde cada curva (A - F) corresponde a las entradas del cuerpo.
        p Esto genera mangas con diferentes volmenes y tipos de ajuste, segn la lnea de sisa seleccionada.
    
    .row.justify-content-center.mb-5
      .col-lg-8.mb-4.mb-lg-0(data-aos="fade-right")
        .titulo-sexto.color-acento-contenido.mb-3
          h5 Figura 23. 
          span Efecto por transformacin de sisa en prenda             
        figure.mb-2
          img(src="@/assets/curso/temas/t2/fig23.png" alt="Presenta cinco figuras alineadas que representan distintas versiones de una prenda con manga amplia. Cada figura, etiquetada de la A a la E, presenta un aumento progresivo en el volumen de la manga desde un diseo ajustado hasta uno muy holgado. La estructura general del cuerpo se mantiene, mientras la forma de la manga vara en amplitud y cada.")
        figcaption Fuente: SENA, (2021)
      .col-lg-4.mt-lg-4(data-aos="fade-left")
        h4 Patrones de la figura 23
        p.mb-1 #[strong Manga A:] la ms ajustada. Sisa alta, cerca del cuello. Ideal para prendas estructuradas o formales. Da una silueta ms entallada.
        p.mb-1 #[strong Manga B:] ligeramente ms baja que la A. Permite algo ms de movilidad, aun manteniendo una forma definida.
        p.mb-1 #[strong Manga C:] curva media. Equilibra comodidad y estilo. Se usa comnmente en prendas deportivas o casuales.
        p.mb-1 #[strong Manga D:] sisa baja y amplia. Proporciona una manga suelta, con ms cada sobre el brazo. Usada en buzos, chaquetas o estilos relajados.
        p.mb-1 #[strong Manga E:] la ms amplia. La sisa cae casi a nivel de la cintura, generando un estilo tipo "murcilago" o "#[em oversize]". Es ideal para diseos de moda con volumen y libertad de movimiento.

    p Las figuras 22 y 23 presentan cmo distintas variaciones en la curva de la sisa (etiquetadas como A, B, C, D y E) generan mangas ragln con diferentes niveles de amplitud, cada y estilo visual.

    p.mb-5 Cada lnea de sisa en el patrn base (A - E) corresponde directamente al tipo de curva en la copa de la manga, generando una transformacin visible en la forma, volumen y cada de la prenda. Esta relacin permite disear desde mangas ajustadas hasta propuestas sueltas y de estilo vanguardista, adaptadas al propsito de la prenda


    .subt2.mb-4.py-2.px-4(data-aos="fade")
      h4.mb-0 A. Manga ragln

    .row.justify-content-center.mb-5
      .col-lg-7.col-md-10.mb-4.mb-lg-0
        .titulo-sexto.color-acento-contenido(data-aos='fade-right')
          h5 Figura 24. 
          span Trazo de manga
        figure.mb-2
          img(src='@/assets/curso/temas/t2/fig24.svg', alt='Presenta el trazo tcnico de una prenda superior con manga tipo kimono. Incluye las piezas del frente y la parte posterior, ambas con la manga incorporada en una sola forma continua. Se sealan medidas en centmetros, lneas de construccin, ubicacin de botones y pieza del cuello en la parte superior.')  
        figcaption Fuente: SENA, (2021)
      .col-lg-5
        AcordionA.style2(tipo="a" clase-tarjeta="tarjeta color-primario")
          .row.justify-content-center(titulo="1. Estructura general del molde")
            .col-11
              p La figura ilustra un patrn tcnico para una blusa o camisa con manga rangln (o ragln). Se representan tres piezas principales:
              ul.lista-ul--color.color-primario.fa-lista.mb-4
                li
                  i.fas.fa-check-circle
                  span.mb-0 A la izquierda, el molde del delantero con manga incorporada.
                li
                  i.fas.fa-check-circle
                  span.mb-0 A la derecha, el molde de espalda, tambin con manga incorporada.
                li
                  i.fas.fa-check-circle
                  span.mb-0 En la parte superior central, una pieza independiente correspondiente al cuello o cuello camisero.
              p Todos los moldes estn dibujados con lneas negras sobre fondo blanco, y contienen medidas numricas en centmetros en cada seccin clave.
          .row.justify-content-center(titulo="2. Caractersticas del delantero")
            .col-11
              p.mb-0 El trazo incluye el cuerpo completo con la manga integrada al patrn.
              p.mb-0 El cuello delantero inicia en el punto ms alto del hombro y desciende hacia el escote, con una curva marcada.
              p.mb-0 Desde ese punto, la lnea de la manga nace suavemente desde el cuello, bajando en ngulo hacia el costado.
              p La manga presenta una cada natural y se extiende hasta una lnea recta que indica el dobladillo o terminacin de manga.
              p Sobre el cuerpo se marcan detalles como:
              ul.lista-ul--color.color-primario.fa-lista
                li
                  i.fas.fa-check-circle
                  span.mb-0  Pinza lateral (lnea diagonal que mejora el ajuste).
                li
                  i.fas.fa-check-circle
                  span.mb-0 Lneas gua para botones y ojales en el centro delantero.
                li
                  i.fas.fa-check-circle
                  span.mb-0 Varias medidas indican distancias desde el cuello, hombro y costados (ej. 4.5 cm, 13.5 cm, 17 cm, etc.).
          .row.justify-content-center(titulo="3. Caractersticas de la espalda")
            .col-11
              p.mb-1 Similar al delantero, la manga forma parte del mismo trazo que el cuerpo trasero.
              p.mb-1 El cuello trasero es ms alto y menos profundo que el delantero.
              p.mb-1 La lnea de la manga se une al cuello con una ligera curva hacia el costado.
              p.mb-1 La parte inferior de la espalda incluye una leve forma curva en el dobladillo para dar holgura.
              p Se sealan pliegues o pinzas en la espalda con lneas verticales, indicando espacio adicional para el movimiento.
          .row.justify-content-center(titulo="4. Pieza de cuello")
            .col-11
              p En la parte superior de la figura aparece la pieza del cuello, una forma alargada y curva con lneas internas para doblado y costura.
              p Est etiquetada como cuello, con medidas horizontales de 17 cm y mrgenes adicionales en ambos extremos.

