      h4.mb-0 B. Manga tulipn

    .row.justify-content-center.mb-5
      .col-lg-7.mb-4.mb-lg-0(data-aos="fade-right")
        AcordionA.style2(tipo="a" clase-tarjeta="tarjeta color-acento-contenido")
