            .col-11
              p.mb-1 Parte superior: diagrama tcnico con medidas.
              p.mb-1 Parte central: piezas A y B mostradas de forma separada.
              p Parte inferior: piezas A y B unidas por los laterales, formando la manga completa.
          .row.justify-content-center(titulo="Trazo de la manga ptalo (parte superior)")
            .col-11
              p.mb-1 La figura principal expone una forma curva dividida verticalmente en dos partes iguales: pieza A (izquierda) y pieza B (derecha).
              p.mb-1 La altura total de cada pieza es de 5 cm.
              p En el centro, hay una lnea vertical de 7.5 cm, con un punto superior marcado a 6 cm de altura que indica la parte ms alta del cruce de las piezas.
              p.mb-1 A cada lado de esta lnea central, se marca un espacio de 1.5 cm hacia los bordes, lo que forma la base recta de la manga.
              p.mb-1 Las lneas curvas que conectan estos puntos crean la caracterstica forma cruzada y redondeada de la manga ptalo.
              p Las lneas de corte estn marcadas como guas para separar las piezas A y B, que se cruzarn una sobre otra.
          .row.justify-content-center(titulo="Piezas separadas (parte central de la figura)")
            .col-11
              p.mb-1 Aqu se exponen las dos mitades del trazo anterior:
              p.mb-1 A la izquierda, pieza A, con forma curvada y una muesca (pequeo corte de gua) en la parte superior.
              p.mb-1 A la derecha, pieza B, simtrica a la anterior.
              p Estas piezas se cortan individualmente y luego se colocan superpuestas en la confeccin.
          .row.justify-content-center(titulo="Piezas unidas (parte inferior de la figura)")
            .col-11
              p En esta seccin, las piezas A y B estn unidas por los costados, mostrando cmo lucen cuando se ensamblan.
              p La unin forma una figura en forma de ptalo o de ala, con una superposicin natural que proporciona movimiento y un acabado decorativo a la manga.
              p Esta unin permite que la manga se abra ligeramente al mover el brazo, generando un efecto suave y elegante.
      .col-lg-5.col-md-6(data-aos='fade-left')
        .titulo-sexto.color-acento-contenido
          h5 Figura 25. 
          span Trazo de manga tulipn 
        figure.mb-2
          img(src='@/assets/curso/temas/t2/fig25.png', alt='Representa el desarrollo de una manga tipo ptalo. En la parte superior se muestra el trazo completo con medidas y divisiones. Al centro, las dos piezas identificadas como A y B estn separadas. En la parte inferior, esas piezas aparecen unidas por los laterales, formando una estructura curva y cruzada.')  
        figcaption Fuente: SENA, (2021)
          

    .row.bgc-3.align-items-center.p-2.p-md-3.mb-4(data-aos="fade-down")
      .col-lg-12.col-12.px-lg-4
        .row.justify-content-center.align-items-center

          .row.justify-content-center
            .col-12
              .subt2.mb-4.py-2.px-4(data-aos="fade")
                h4.mb-0 C. Manga Kimono
            
            .col-lg-7.col-md-8.mb-4.mb-lg-0
              .titulo-sexto.color-acento-contenido
                h5 Figura 26. 
                span Trazo de manga kimono 
              figure.mb-2
                img(src='@/assets/curso/temas/t2/fig26.svg', alt='Presenta el trazo tcnico de una prenda superior con manga tipo kimono. Incluye las piezas del frente y la parte posterior, ambas con la manga integrada. Se detallan medidas en centmetros, la ubicacin del cuello y un ejemplo visual de la prenda terminada en el centro inferior.')  
              figcaption Fuente: SENA, (2021)
            .col-lg-5
              LineaTiempoD.color-acento-contenido
                .row(numero="1" titulo="Descripcin") 
                  .col-12
                    p La figura ilustra el desarrollo del patrn de una prenda superior con manga kimono, tpica de blusas o chaquetas amplias, en este caso similar a una blusa tipo chef. El patrn est compuesto por:
                    ul.lista-ul--color.color-primario.fa-lista.mb-4
                      li.mb-1
                        i.fas.fa-check-circle
                        span.mb-0 Molde delantero (izquierda).
                      li.mb-1
                        i.fas.fa-check-circle
                        span.mb-0 Molde trasero (derecha).        
                      li.mb-1
                        i.fas.fa-check-circle
                        span.mb-0 Pieza del cuello (centro superior).
                      li
                        i.fas.fa-check-circle
                        span.mb-0 Vista frontal ilustrada de la prenda terminada (centro inferior).
                .row(numero="2" titulo="Caractersticas del molde delantero") 
                  .col-12
                    p.mb-1 La manga est integrada al cuerpo del patrn, formando una sola pieza continua que va desde el cuello hasta el final de la manga, sin costura de hombro.
                    p El cuello se curva suavemente desde el centro delantero hacia el hombro.
                    p.mb-1 Se incluye un doblez o cruce frontal, con lneas marcadas para abotonadura doble (caracterstica tpica en uniformes o prendas de chef).
                    p Las medidas en centmetros indican:
                    ul.lista-ul--color.color-primario.fa-lista.mb-4
                      li.mb-1
                        i.fas.fa-check-circle
