    #t_2_3.titulo-segundo.color-acento-contenido(data-aos='fade-right')
      h2 2.3 Vestido casual

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-5.col-md-8.mb-4.mb-lg-0(data-aos="fade-right")
        img(src="@/assets/curso/temas/t2/12.png")
      .col-lg-7(data-aos="fade-left")
        img.w-85.mb-3.d-none.d-lg-block(src="@/assets/curso/temas/t2/13.svg")
        p El vestido sin corte en la cintura es una monoprenda en donde se emplea el patrn base superior y se prolonga el largo. En otros casos se puede unir con moldes de faldas logrando diferentes tipologas de vestidos.
        .tarjeta.bg-2.p-4.br-5
          p.mb-0 #[strong Pasos detallados para realizar el trazo del vestido:] el vestido clsico sin unin en la cintura se puede transformar en un sinfn de otros bsicos, son solo acciones como, dar amplitud, cambiar el largo con ello, se obtiene una forma y da paso a una nueva silueta. A continuacin, se presentan los pasos detallados para realizar la base de superior femenina: 
    
    .row.justify-content-center.mb-2
      .col-lg-8(data-aos="fade-right")
        LineaTiempoD.color-secundario
          .row(numero="1" titulo="Primer paso para el trazo") 
            .col-md-4
              p Se debe trazar una lnea vertical y marcar la ubicacin correspondiente a la lnea de cintura. Desde ese punto, se mide hacia abajo la altura de cadera. En la lnea de cadera, se dibuja una lnea horizontal utilizando  del contorno de cadera del cliente. Desde el extremo de esta lnea, se escuadra hacia arriba hasta alcanzar la lnea de cintura. Sobre esta ltima, a partir del punto de interseccin, se mide una distancia de 2 cm y se seala un nuevo punto. Finalmente, el patrn delantero se posiciona haciendo coincidir dicho punto con el cruce de las lneas previamente determinadas. 
            .col-md-7.col-10
              .titulo-sexto.color-acento-contenido.mb-2
                h5 Figura 27. 
                span Primer paso para el trazo 
              figure.mb-2
                img(src='@/assets/curso/temas/t2/fig27.svg', alt='Representa un ajuste en el trazo del delantero de una prenda superior. Una lnea diagonal conecta el escote con el punto del busto y est marcada como rea de corte. La base del molde se desplaza 2 centmetros hacia el costado. En la parte inferior se aade un rectngulo con la altura de la cadera y un cuarto de su medida para extender el largo hacia esa zona.')  
              figcaption Fuente: SENA, (2021)
          .row(numero="2" titulo="Segundo paso para el trazo ") 
            .col-md-4
              p Marcar un punto en la mitad del largo hombro y hacer corte hasta el punto se separacin de busto. Girar el patrn abriendo esta nueva pinza y cerrando la pinza de cintura hasta que la pinza de la cintura quede sobre la lnea horizontal.
            .col-md-7.col-10
              .titulo-sexto.color-acento-contenido.mb-2
                h5 Figura 28. 
                span Segundo paso para el trazo 
              figure.mb-2
                img(src='@/assets/curso/temas/t2/fig28.svg', alt='Representa una transformacin en el molde delantero de una prenda superior. La pieza se ha cortado desde el escote hasta el punto del busto, y desde la base hasta la pinza lateral. Luego, se gira una de las secciones hasta que la lnea del costado se alinea con el punto del busto. Esta modificacin permite ajustar el volumen en esa zona.')  
              figcaption Fuente: SENA, (2021)
          .row(numero="3" titulo="Tercer paso para el trazo") 
            .col-md-5
              p Dibujar y se corta una lnea desde el punto de busto (punto de pivote) hacia el costado, a 8 cm.
            .col-md-7.col-10
              .titulo-sexto.color-acento-contenido.mb-2
                h5 Figura 29. 
                span Tercer paso para el trazo
              figure.mb-2
                img(src='@/assets/curso/temas/t2/fig29.svg', alt='Dibujo tcnico del tercer paso para el trazo del delantero, mostrando la lnea de cierre depinza y el centro del busto.')  
              figcaption Fuente: SENA, (2021)
          .row(numero="4" titulo="Cuarto paso para el trazo") 
            .col-md-5
              p Cerrar la pinza de hombro, hacerlo trasladando esta pinza hacia el costado, as queda finalizada la pinza de busto. Marcar un punto a 2,5 cm para el inicio de pinza desde el punto de busto y trazamos la pinza de costado. 
            .col-md-7.col-10
              .titulo-sexto.color-acento-contenido.mb-2
                h5 Figura 30. 
                span Cuarto paso para el trazo 
              figure.mb-2
                img(src='@/assets/curso/temas/t2/fig30.svg', alt='Representa una variacin en el molde delantero de una prenda superior. Se ha trazado y cortado una lnea inclinada desde el costado hacia el punto del busto. La seccin superior se ha cerrado 2.5 centmetros, generando un ajuste en la parte alta del busto.')  
              figcaption Fuente: SENA, (2021)
          .row(numero="5" titulo="Quinto paso para el trazo ") 
            .col-md-5
              p Dibujar una lnea horizontal al centro delantero 2,5 cm por debajo de la altura del busto.
            .col-md-7.col-10
              .titulo-sexto.color-acento-contenido.mb-2
                h5 Figura 31. 
                span Quinto paso para el trazo 
              figure.mb-2
                img(src='@/assets/curso/temas/t2/fig31.svg', alt='Representa un trazo tcnico del delantero de una prenda superior con ajustes en el rea del busto y talle. Se indican dos lneas horizontales con desplazamientos de 2.5 centmetros hacia abajo y varias pinzas verticales distribuidas en el centro. Cada pinza mide 1 centmetro de ancho, y en conjunto abarcan 4.5 centmetros, permitiendo dar forma y volumen a la prenda en esa zona.')  
              figcaption Fuente: SENA, (2021)
          .row(numero="6" titulo="Sexto paso para el trazo") 
            .col-md-5
              p Trazar la lnea de costado y las pinzas frente.
            .col-md-7.col-10
              .titulo-sexto.color-acento-contenido.mb-2
                h5 Figura 32. 
                span Sexto paso para el trazo 
              figure.mb-2
                img(src='@/assets/curso/temas/t2/fig32.svg', alt='Representa el trazo del delantero bsico para blusa o vestido. Incluye dos pinzas verticales en la zona del busto y cintura, utilizadas para dar forma al contorno del cuerpo. Tambin se observa una ligera curva en el costado que acompaa la silueta.')  
              figcaption Fuente: SENA, (2021)
          .row(numero="7" titulo="Sptimo paso para el trazo") 
            .col-md-5
              p En el patrn espalda, dibujar una lnea vertical para ubicar el molde, marcar la lnea de cintura y prolongar hacia abajo la altura de cadera y  de contorno cadera siguiendo la referencia de la figura 33. Por el costado hacer una lnea paralela de 3,75 cm de ancho.
            .col-md-7.col-10
              .titulo-sexto.color-acento-contenido.mb-2
                h5 Figura 33. 
                span Sptimo paso para el trazo
              figure.mb-2
                img(src='@/assets/curso/temas/t2/fig33.svg', alt='Representa el trazo tcnico de la parte posterior de una prenda superior. Se indica un corte vertical de 3.75 centmetros en el centro, acompaado por un desplazamiento de 2 centmetros hacia el costado. En la parte inferior se aade un rectngulo con altura de cadera y un cuarto de su medida para extender el molde hacia abajo.')  
              figcaption Fuente: SENA, (2021)
          .row(numero="8" titulo="Octavo paso para el trazo") 
            .col-md-5
              p Hacer corte por la nueva lnea y abrir la pinza hacia afuera 2 cm por la cintura.
            .col-md-7.col-10
              .titulo-sexto.color-acento-contenido.mb-2
                h5 Figura 34. 
                span Octavo paso para el trazo 
              figure.mb-2
                img(src='@/assets/curso/temas/t2/fig34.svg', alt='Representa una modificacin en el trazo de la parte posterior de una prenda superior. Una seccin lateral ha sido cortada y girada hasta alinear su borde con un punto especfico en la base, generando un ajuste en el contorno. La parte inferior incluye una extensin rectangular correspondiente a la altura de cadera.')  
              figcaption Fuente: SENA, (2021)
          .row(numero="9" titulo="Noveno paso para el trazo") 
            .col-md-5
              p Trazar la lnea de costado formando la cadera con suave curva.
            .col-md-7.col-10
              .titulo-sexto.color-acento-contenido.mb-2
                h5 Figura 35. 
                span Noveno paso para el trazo 
              figure.mb-2
                img(src='@/assets/curso/temas/t2/fig35.svg', alt='Representa el trazo final de la parte posterior de una prenda superior con extensin hacia la cadera. El contorno incluye pinza, escote, sisa y un costado con curva suave. En la base se une un rectngulo que completa el largo total de la prenda.')  
              figcaption Fuente: SENA, (2021)
          .row(numero="10" titulo="Dcimo paso para el trazo") 
            .col-md-5
